import React, { useState } from 'react';
import { Tabs, Tab, Box, Typography, Link, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, useMediaQuery } from '@mui/material';
import AppleIcon from '@mui/icons-material/Apple';
import MicrosoftIcon from '@mui/icons-material/Microsoft';
import DesktopWindowsIcon from '@mui/icons-material/DesktopWindows';
import { useTheme } from '@mui/material/styles';

const TabPanel = ({ children, value, index }) => {
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`tabpanel-${index}`}
            aria-labelledby={`tab-${index}`}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
};

const Download = () => {
    const theme = useTheme();
    const [tabIndex, setTabIndex] = useState(0);
    const [macTabIndex, setMacTabIndex] = useState(0);
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    const handleChange = (event, newIndex) => {
        setTabIndex(newIndex);
    };

    const handleMacTabChange = (event, newIndex) => {
        setMacTabIndex(newIndex);
    };

    const renderTable = (osData) => (
        <TableContainer component={Paper} sx={{ mt: 2 }}>
            <Table>
                <TableHead>
                    <TableRow
                        sx={{
                            backgroundColor: theme.palette.grey[700], // Primary blue
                            color: '#fff', // White text
                            '& .MuiTableCell-root': {
                                color: '#fff', // Ensures all TableCells in this row inherit white text
                                fontWeight: 'bold', // Makes the text bold
                            },
                        }}
                    >
                        <TableCell>OS</TableCell>
                        <TableCell>Version</TableCell>
                        <TableCell>Download</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {osData.map((row, index) => (
                        <TableRow key={index}>
                            <TableCell>{row.os}</TableCell>
                            <TableCell>{row.version}</TableCell>
                            <TableCell>
                                <Link href={row.link} download>
                                    Download
                                </Link>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );

    const macData = [
        { os: 'mac x64 (dmg)', version: 'v1.5.4', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.4/hawkClient-1.5.4-x64-mac.dmg' },
        { os: 'mac x64 (zip)', version: 'v1.5.4', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.4/hawkClient-1.5.4-x64-mac.zip' },
        { os: 'mac x64 (dmg)', version: 'v1.5.3', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.3/hawkClient-1.5.3-x64-mac.dmg' },
        { os: 'mac x64 (zip)', version: 'v1.5.3', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.3/hawkClient-1.5.3-x64-mac.zip' },
    ];

    const windowsData = [
        { os: 'Windows x64', version: 'v1.5.4', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.4/hawkClient-1.5.4-x64-win.exe' },
        { os: 'Windows x64', version: 'v1.5.3', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.3/hawkClient-1.5.3-x64-win.exe' },
    ];

    const linuxData = [
        { os: 'Linux AppImage', version: 'v1.5.4', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.4/hawkClient-1.5.4-arm64-linux.AppImage' },
        { os: 'Linux AppImage', version: 'v1.5.3', link: 'https://github.com/prashantrathi123/hawkClient/releases/download/v1.5.3/hawkClient-1.5.3-arm64-linux.AppImage' },
    ];

    return (
        <Box sx={{ padding: '20px', display: 'flex', flexDirection: 'column', alignItems: 'center', minHeight: '100vh' }}>
            {/* Horizontal Tabs */}
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    gap: '8px',
                    marginTop: '10px'
                }}
            >
                <Typography variant="h5" component="h1">Download the HawkClient app today</Typography>
                <Tabs
                    value={tabIndex}
                    onChange={handleChange}
                    centered // Center tabs within the container
                    textColor="primary"
                    indicatorColor="primary"
                >
                    <Tab label="Mac" icon={<AppleIcon />} iconPosition="start" />
                    <Tab label="Windows" icon={<MicrosoftIcon />} iconPosition="start" />
                    <Tab label="Linux" icon={<DesktopWindowsIcon />} iconPosition="start" />
                </Tabs>
            </Box>

            {/* Mac Tab Panel with Vertical Tabs */}
            <Box sx={{ width: { md: '70%' } }}>
                <TabPanel value={tabIndex} index={0}>
                    <Box sx={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row', mt: 2 }}>
                        {/* Vertical Tabs */}
                        <Tabs
                            orientation={isMobile ? "horizontal" : "vertical"}
                            value={macTabIndex}
                            onChange={handleMacTabChange}
                            sx={{ borderRight: isMobile ? 0 : 1, borderColor: 'divider', minWidth: 120, alignSelf: isMobile ? 'center' : 'start' }}
                        >
                            <Tab label="Download" />
                            <Tab label="Install" />
                        </Tabs>

                        {/* Content for Download and Install Tabs */}
                        <Box sx={{ flexGrow: 1, p: 2 }}>
                            {/* Mac Download Tab Panel */}
                            <TabPanel value={macTabIndex} index={0}>
                                <Typography variant="h6">Download for Mac</Typography>
                                {renderTable(macData)}
                                <Typography style={{ marginTop: '8px' }}>Note: Please refer to the 'Install' tab for assistance with resolving Mac installation issues.</Typography>
                            </TabPanel>

                            {/* Mac Install Tab Panel */}
                            <TabPanel value={macTabIndex} index={1}>
                                <Typography variant="h6" align="center">
                                    First, download the file from the "Download" tab, then double-click the file to start the installation process.
                                </Typography>

                                <Typography variant="body1" align="center" sx={{ mt: 2 }}>
                                    If macOS blocks the installation, you can use the following shell script to disable Gatekeeper:
                                </Typography>

                                <Box
                                    component="pre"
                                    sx={{
                                        backgroundColor: '#f5f5f5',
                                        padding: '10px',
                                        borderRadius: '5px',
                                        mt: 2,
                                        overflow: 'auto',
                                        whiteSpace: 'pre-wrap',
                                        textAlign: 'left',
                                    }}
                                >
                                    <Typography component="code" sx={{ color: '#d32f2f' }}>
                                        {`sudo spctl --master-disable`}
                                    </Typography>
                                </Box>
                            </TabPanel>
                        </Box>
                    </Box>
                </TabPanel>

                {/* Windows Tab Panel */}
                <TabPanel value={tabIndex} index={1}>
                    <Box sx={{ display: 'flex', mt: 2 }}>
                        <Box sx={{ flexGrow: 1, p: 2 }}>
                            <Typography variant="h6">Download for Windows</Typography>
                            {renderTable(windowsData)}
                        </Box>
                    </Box>
                </TabPanel>

                {/* Linux Tab Panel */}
                <TabPanel value={tabIndex} index={2}>
                    <Box sx={{ display: 'flex', flexDirection: 'column', mt: 2 }}>
                        <Box sx={{ flexGrow: 1, p: 2 }}>
                            <Typography variant="h6">Download for Linux</Typography>
                            {renderTable(linuxData)}
                        </Box>
                    </Box>
                </TabPanel>
            </Box>
        </Box>
    );
};

export default Download;
